*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* border: 1px solid red; */
}

.form-box{
    min-width: 100%;
    height: 100vh;
    /* border: dotted #F55A00 5vw; */
    /* border: 1px solid red; */
    margin: 0vw;
    padding: 5vw ;
    position:relative; 
    /* position:relative for .prevBtn and .nextBtn position:absolute; */
}


.form-box h5{
    font-size: 35px ;
    text-align: center;
    color: #F55A00;
    margin: 0 0 .5vh 0;
}


.form-box label{
    display: block;
    text-align: center;
    font-size: 20px;
    margin: 0 0 2vh 0;
}
.btn--sbm {
    background-color: rgb(85, 85, 169);
    color: #242424;
    border: 1px solid var(--primary);
    padding: 1rem 1.5rem;
    justify-content: center;
    border-radius: 30px;
    font-size: 1.8rem;
}
.form-box input, textarea{
    display: block;
    width: 100%;
    padding: .5rem .8rem .5rem .8rem;
    margin: .9vw 0 ;
    border:0.5px solid blueviolet;
    border-radius: 5px;
    font-size: 20px;
}

.form-box textarea{
    height: 15vh;
}


.prevBtn, .nextBtn{
    display: inline-block;
    width: 30%;
    height: 2rem;
    color: red;
    border: 0;
    bottom: 0;
    cursor: pointer;
    font-size: 20px;
    position:absolute;

}

.prevBtn{
    margin: 1rem 0 1rem 0;
    border-radius: 20px 0 0 20px; 
}

.nextBtn{
    margin: 1rem 0 1rem 8rem;
    margin-left: 52%;   
    border-radius: 0 20px 20px 0; 
}

.nextBtn:hover, .prevBtn:hover{
    background-color: rgb(245, 245, 78);
    transition: 1s background ease;
}

/*margin: top right bottom left;*/
.nextBtn{    
    margin-left: 52%;
}
.prevBtn, .nextBtn{    
    margin: 1rem 0 1rem 0; 
    display: inline-block;   
}

